'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { poiData, POI } from '@/lib/poiData';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';

interface UserLocation {
  latitude: number;
  longitude: number;
}

export default function Map() {
  const router = useRouter();
  const [userLocation, setUserLocation] = useState<UserLocation | null>(null);
  const [locationError, setLocationError] = useState<string>('');
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    getCurrentLocation();
  }, []);

  const getCurrentLocation = () => {
    setIsLoading(true);
    setLocationError('');

    if (!navigator.geolocation) {
      setLocationError('Geolocation is not supported by this browser.');
      setIsLoading(false);
      return;
    }

    navigator.geolocation.getCurrentPosition(
      (position) => {
        setUserLocation({
          latitude: position.coords.latitude,
          longitude: position.coords.longitude,
        });
        setIsLoading(false);
      },
      (error) => {
        console.error('Geolocation error:', error);
        let errorMessage = 'Unable to retrieve your location.';
        
        switch (error.code) {
          case error.PERMISSION_DENIED:
            errorMessage = 'Location access denied. Please enable location services and try again.';
            break;
          case error.POSITION_UNAVAILABLE:
            errorMessage = 'Location information is unavailable.';
            break;
          case error.TIMEOUT:
            errorMessage = 'Location request timed out.';
            break;
        }
        
        setLocationError(errorMessage);
        setIsLoading(false);
      },
      {
        enableHighAccuracy: true,
        timeout: 10000,
        maximumAge: 60000,
      }
    );
  };

  const calculateDistance = (lat1: number, lon1: number, lat2: number, lon2: number): number => {
    const R = 6371; // Radius of the Earth in kilometers
    const dLat = (lat2 - lat1) * Math.PI / 180;
    const dLon = (lon2 - lon1) * Math.PI / 180;
    const a = 
      Math.sin(dLat/2) * Math.sin(dLat/2) +
      Math.cos(lat1 * Math.PI / 180) * Math.cos(lat2 * Math.PI / 180) * 
      Math.sin(dLon/2) * Math.sin(dLon/2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
    return R * c;
  };

  const handlePOIClick = (poiId: string) => {
    router.push(`/poi/${poiId}`);
  };

  const getRelativePosition = (poi: POI, userLat: number, userLon: number) => {
    // Calculate relative position for visual representation
    const latDiff = (poi.latitude - userLat) * 1000; // Scale for visibility
    const lonDiff = (poi.longitude - userLon) * 1000;
    
    // Convert to percentage positions (centered map)
    const x = 50 + (lonDiff * 10); // Scale and center
    const y = 50 - (latDiff * 10); // Invert Y axis and center
    
    // Clamp to map boundaries
    return {
      x: Math.max(5, Math.min(95, x)),
      y: Math.max(5, Math.min(95, y))
    };
  };

  if (isLoading) {
    return (
      <div className="w-full h-96 flex items-center justify-center">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-blue-600 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-gray-600">Getting your location...</p>
        </div>
      </div>
    );
  }

  if (locationError) {
    return (
      <div className="w-full max-w-2xl mx-auto p-4">
        <Alert className="mb-4">
          <AlertDescription>{locationError}</AlertDescription>
        </Alert>
        <div className="text-center">
          <Button onClick={getCurrentLocation}>
            Try Again
          </Button>
        </div>
      </div>
    );
  }

  return (
    <div className="w-full">
      {/* Map Container */}
      <div className="relative bg-green-50 border-2 border-green-200 rounded-lg overflow-hidden" style={{ height: '500px' }}>
        {/* Map Background Pattern */}
        <div className="absolute inset-0 opacity-20">
          <div className="grid grid-cols-10 grid-rows-10 h-full">
            {Array.from({ length: 100 }).map((_, i) => (
              <div key={i} className="border border-green-300"></div>
            ))}
          </div>
        </div>

        {/* User Location Marker */}
        {userLocation && (
          <div 
            className="absolute transform -translate-x-1/2 -translate-y-1/2 z-20"
            style={{ left: '50%', top: '50%' }}
          >
            <div className="relative">
              <div className="w-4 h-4 bg-blue-600 rounded-full border-2 border-white shadow-lg"></div>
              <div className="absolute -top-8 left-1/2 transform -translate-x-1/2 bg-blue-600 text-white px-2 py-1 rounded text-xs whitespace-nowrap">
                You are here
              </div>
              {/* Pulse animation */}
              <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-8 h-8 bg-blue-400 rounded-full opacity-30 animate-ping"></div>
            </div>
          </div>
        )}

        {/* POI Markers */}
        {userLocation && poiData.map((poi) => {
          const position = getRelativePosition(poi, userLocation.latitude, userLocation.longitude);
          const distance = calculateDistance(
            userLocation.latitude, 
            userLocation.longitude, 
            poi.latitude, 
            poi.longitude
          );

          return (
            <div
              key={poi.id}
              className="absolute transform -translate-x-1/2 -translate-y-1/2 z-10 cursor-pointer"
              style={{ 
                left: `${position.x}%`, 
                top: `${position.y}%` 
              }}
              onClick={() => handlePOIClick(poi.id)}
            >
              <div className="relative group">
                <div className="w-6 h-6 bg-red-600 rounded-full border-2 border-white shadow-lg hover:bg-red-700 transition-colors"></div>
                <div className="absolute -top-12 left-1/2 transform -translate-x-1/2 bg-gray-900 text-white px-3 py-2 rounded text-sm whitespace-nowrap opacity-0 group-hover:opacity-100 transition-opacity pointer-events-none">
                  <div className="font-semibold">{poi.title}</div>
                  <div className="text-xs text-gray-300">{distance.toFixed(1)} km away</div>
                  <div className="absolute top-full left-1/2 transform -translate-x-1/2 border-4 border-transparent border-t-gray-900"></div>
                </div>
              </div>
            </div>
          );
        })}

        {/* Map Legend */}
        <div className="absolute bottom-4 left-4 bg-white p-3 rounded-lg shadow-lg">
          <h3 className="font-semibold text-sm mb-2">Map Legend</h3>
          <div className="space-y-1 text-xs">
            <div className="flex items-center">
              <div className="w-3 h-3 bg-blue-600 rounded-full mr-2"></div>
              <span>Your Location</span>
            </div>
            <div className="flex items-center">
              <div className="w-3 h-3 bg-red-600 rounded-full mr-2"></div>
              <span>Heritage Sites</span>
            </div>
          </div>
        </div>

        {/* Compass */}
        <div className="absolute top-4 right-4 bg-white p-2 rounded-full shadow-lg">
          <div className="w-8 h-8 flex items-center justify-center text-sm font-bold">N</div>
        </div>
      </div>

      {/* POI List */}
      <div className="mt-6">
        <h2 className="text-2xl font-bold text-gray-900 mb-4">Heritage Sites</h2>
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-4">
          {poiData.map((poi) => {
            const distance = userLocation 
              ? calculateDistance(userLocation.latitude, userLocation.longitude, poi.latitude, poi.longitude)
              : 0;

            return (
              <Card 
                key={poi.id} 
                className="cursor-pointer hover:shadow-lg transition-shadow"
                onClick={() => handlePOIClick(poi.id)}
              >
                <CardHeader>
                  <CardTitle className="text-lg">{poi.title}</CardTitle>
                  <CardDescription>
                    {userLocation && `${distance.toFixed(1)} km away`}
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <p className="text-gray-600 text-sm line-clamp-3">
                    {poi.description}
                  </p>
                  <Button className="mt-3 w-full" variant="outline" size="sm">
                    View Details
                  </Button>
                </CardContent>
              </Card>
            );
          })}
        </div>
      </div>
    </div>
  );
}
